import React from 'react';
import {motion } from 'framer-motion'

const UserPhoto = ({ photoURL, onClick }) => {
  return (
    <motion.img
      whileTap={{ scale: 0.6 }}
      src={photoURL}
      alt="Avatar"
      className="w-10 h-10 min-w-[40px] min-h-[10px] drop-shadow-xl cursor-pointer rounded-full "
      onClick={onClick}
    />
  );
};

export default UserPhoto;
